:root {
  --color-1: #fff;
  --color-2: #595758;
  --color-3: #cc8265;
}

main {
  max-width: 80%;
}
main p {
  max-width: 35em;
  margin-left: auto;
  margin-right: auto;
}

header {
  max-width: 80%;
  margin: 2em auto 0;
  text-align: center;
  animation: fadeIn;
  animation-duration: 1s;
}
header p {
  text-transform: uppercase;
  letter-spacing: 0.2em;
  color: var(--color-3);
}

h1 {
  font-size: 4em;
  margin-top: 2rem;
  line-height: 1;
}

section + section {
  margin-top: 2em;
}

#info {
  font-style: italic;
}
#info p + p {
  margin-top: 0;
}

#sketches {
  max-width: 35em;
  margin-left: auto;
  margin-right: auto;
}